import { HISTORY_FETCH } from "src/actions/historyActions";

export const initialState = {
  isFetching: false,
  error: null,
  value: {},
};

export default (state = initialState, { type, payload, error }) => {
  switch (type) {
    case HISTORY_FETCH.PENDING:
      return {
        ...state,
        isFetching: true,
      };
    case HISTORY_FETCH.SUCCESS:
      return {
        ...state,
        isFetching: false,
        value: payload,
        error: null,
      };
    case HISTORY_FETCH.FAILED:
      return {
        ...state,
        isFetching: false,
        error,
        value: {},
      };
    default:
      return state;
  }
};
