/* eslint-disable no-param-reassign */
import produce from 'immer';
import {
  INITIAL_CREATE,
  UPDATE_SET_DETAIL_FORM,
  UPDATE_SET_SELECT_TOOLS_FORM,
  RESET_FORM
} from 'src/actions/setsActions';

const initialState = {
  set: {
    id: '',
    name: '',
    type: '',
    active: false,
    hospital: '',
    media: '',
    description: '',
    equipments: {
      byId: { /* key  equipment id  value  object  equipment  property "amount"  */ },
      allIds: [ /* list  equipment id */],
      totalEquipments: 0,
      totalItems: 0
    },
  }
};

const setCreateReducer = (state = initialState, {type, payload}) => {
  switch(type){
    case INITIAL_CREATE: {
      console.log('payload: ', payload)

      if(!payload){
        return produce(state, (draft)=> {
          // Ensure we clear current session
          Object.keys(initialState.set).forEach((k)=> {
            draft.set[k] = initialState.set[k]
          })
        })
      }
      else{
        return produce(state, (draft)=> {
          Object.keys(initialState.set).forEach((key)=> {
            // if(key === 'resource') return;
            // if(key === 'equipmentIds'){
            //   const equipmentIds = payload[key];
            //   const setId = payload['id'];
            //   const resource = payload['resource'];
            //   const equipments = payload["equipments"];
            //   const resourceItems = resource[setId];
            //   const byId = {};

            //   let totalItems = equipmentIds
            //     ?.map(equipmentId => {
            //       byId[equipmentId] = equipments[equipmentId];
            //       return resourceItems[equipmentId];
            //     })
            //     .reduce((acc, cur) => acc + cur, 0);
              
            //   totalItems = totalItems ? totalItems: 0;  
                
              // draft.set[key] = {
              //   byId: byId,
              //   allIds: [...equipmentIds],
              //   totalEquipments: equipmentIds.length,
              //   totalItems: totalItems
              // }
            //}
            //else 
            draft.set[key] = payload[key]
          })
        })
      }
    }
    case UPDATE_SET_DETAIL_FORM: {
      const { id, type, name, hospital, active, description } = payload

      return produce(state, (draft)=> {
        draft.set.id = id
        draft.set.type = type
        draft.set.name = name
        draft.set.hospital = hospital
        draft.set.active = active
        draft.set.description = description
      })
    }
    case UPDATE_SET_SELECT_TOOLS_FORM: {
      const {byId, allIds, totalEquipments, totalItems} = payload

      return produce(state, (draft)=> {
        draft.set.equipments.byId = byId
        draft.set.equipments.allIds = allIds
        draft.set.equipments.totalEquipments = totalEquipments
        draft.set.equipments.totalItems = totalItems
      })
    }
    case RESET_FORM: {
      return produce(state, (draft)=> {
        // Ensure we clear current session
        Object.keys(initialState.set).forEach((k)=> {
          draft.set[k] = initialState.set[k]
        })
      })
    }
    default: {
      return state
    }
  }
}

export default setCreateReducer