import _ from 'lodash';
import jwt from 'jsonwebtoken';
import mock from 'src/utils/mock';

const JWT_SECRET = 'arcadia-secret-key';
const JWT_EXPIRES_IN = '2 days';

const db = {
  user: {
    id: '5e86809283e28b96d2d38537',
    avatar: '/static/images/avatars/mook.jpg',
    bio: 'Administrator',
    country: 'Thailand',
    email: 'chotmanee.t@arcadiaapm.com',
    username: 'mooking',
    password: 'mook',
    firstName: 'Chotmanee',
    lastName: 'Thong-aom',
    phone: '+66 0960460583',
    role: 'admin',
    state: 'Bangkok',
    timezone: '4:32PM (GMT+7)'
  }
};

mock.onPost('/api/account/login').reply((config) => {
  const { email, password } = JSON.parse(config.data);

  if (email !== 'chotmanee.t@arcadiaapm.com' || password !== 'mook') {
    return [400, { message: 'Please check your email and password' }];
  }

  const { user } = db;

  const accessToken = jwt.sign(
    { id: user.id },
    JWT_SECRET,
    { expiresIn: JWT_EXPIRES_IN }
  );

  return [200, { user, accessToken }];
});

mock.onGet('/api/account/me').reply((config) => {
  const { Authorization } = config.headers;

  if (!Authorization) {
    return [401, { message: 'Authorization token missing' }];
  }

  try {
    const accessToken = Authorization.split(' ')[1];

    const { id } = jwt.verify(accessToken, JWT_SECRET);

    if (id !== db.user.id) {
      return [401, { message: 'Invalid authorization token' }];
    }

    return [200, { user: db.user }];
  } catch (error) {
    return [401, { message: 'Invalid authorization token' }];
  }
});

mock.onPost('/api/account/profile').reply((request) => {
  const { update } = JSON.parse(request.data);

  _.assign(db.user, update);

  return [200, { user: db.user }];
});

mock.onGet('/api/account/settings').reply(200, {
  settings: {}
});

mock.onPost('/api/account/forgot-password').reply((request) => {
  const email = request.data;

  if (email === '') {
    return [401, { message: 'Incorrect or invalid email address.' }];
  }

  return [200, { message: 'Email has been sent successfully.'}];
});

mock.onPost('/api/account/reset-password').reply((request) => {
  const { newPassword, confirmPassword } = JSON.parse(request.data);

  if (newPassword !== confirmPassword) {
    return [401, { message: 'Passwords do not match.' }];
  }

  return [200, { message: 'Password has been changed successfully.'}];
});