export const INITIAL_CREATE = '@sets/initial-create'
export const UPDATE_SET_DETAIL_FORM = '@sets/create-set/step1'
export const UPDATE_SET_SELECT_TOOLS_FORM = '@sets/create-set/step2'
export const RESET_FORM = '@sets/reset-form'

export function initCreate(set){
  return async (dispatch) => {
    dispatch({
      type: INITIAL_CREATE,
      payload: set ? {...set} : null
    })
  }
}

export function updateInformationForm({id, name, type, hospital, description, active}){
  return async (dispatch) => {
    dispatch({
      type: UPDATE_SET_DETAIL_FORM,
      payload: {
        id: id,
        name: name,
        hospital: hospital,
        description: description,
        type: type,
        active: active
      }
    })
  }
}

export function updateSelectEquipmentsForm(selectedItems){
  return async (dispatch) => {
    dispatch({
      type: UPDATE_SET_SELECT_TOOLS_FORM,
      payload: selectedItems
    })
  }
}

export function resetForm(){
  return async (dispatch) => {
    dispatch({
      type: RESET_FORM
    })
  }
}