import axios from 'src/utils/axios';
import { createActionSet } from 'src/helpers';

export const EQUIPMENT_FETCH = createActionSet('@equipment/fetch')
export const EQUIPMENT_CREATE = createActionSet('@equipment/create')
export const EQUIPMENT_UPDATE = createActionSet('@equipment/update')
export const EQUIPMENT_DELETE = createActionSet('@equipment/delete')

export const createFormEQ = (formStep) => async (dispatch) => {
  dispatch({
    type: EQUIPMENT_CREATE.SUCCESS,
    payload: formStep,
  });
};

export const saveFormEQ = (dataForm) => async (dispatch) => {
  console.log(dataForm);
  dispatch({
    type: EQUIPMENT_UPDATE.PENDING,
  });

  dispatch({
    type: EQUIPMENT_UPDATE.SUCCESS,
    payload: dataForm,
  });
};

export const deleteEQ = (deleteData) => async (dispatch) => {
  dispatch({
    type: EQUIPMENT_DELETE.PENDING,
  });
  dispatch({
    type: EQUIPMENT_DELETE.SUCCESS,
    payload: deleteData,
  });
};

export const getAllListEquipment = (didCancel) => async (dispatch) => {
  let url = process.env.REACT_APP_API_URL + "/equipments";

  dispatch({
    type: EQUIPMENT_FETCH.PENDING,
  });

  try {
    const [result] = await Promise.all([axios.get(url)]);
    if (!didCancel) {
      dispatch({
        type: EQUIPMENT_FETCH.SUCCESS,
        payload: result.data,
      });
    }
  } catch (error) {
    if (!didCancel) {
      dispatch({
        type: EQUIPMENT_FETCH.FAILED,
        error,
      });
    }
  }
};
