import mock from 'src/utils/mock'
import * as dayjs from 'dayjs'
import { v4 as uuidv4 } from 'uuid'
import _ from 'lodash'
import pagingService from 'src/services/pagingService'
import { SurgicalTypesMock, SurgicalTypeMedicalToolsMock, MedicalToolsMock } from './mock-data/surgicalTypes'

mock.onGet('/api/sets').reply(() => new Promise(((resolve) => {
  const dataItems = [...SurgicalTypesMock]
  setTimeout(() => {
    resolve([200, {
      data: dataItems,
      total: dataItems.length,
      page: 0,
      pageSizes: 10
    }])
  }, 1000)
})))

mock.onGet('/api/sets/fetch').reply((request) => new Promise((resolve, reject) => {
  const { query, sort, page, limit } = request?.params
  
  let dataItems = [...SurgicalTypesMock]

  const filteredItems = pagingService.applyFilters(dataItems, query, ['name'])
  const sortedItems = pagingService.applySort(filteredItems, sort)
  const paginatedItems = pagingService.applyPagination(sortedItems, page, limit)
  
  setTimeout(() => {
    resolve([200, {
      data: [...paginatedItems],
      total: filteredItems.length,
      errors: null
    }])
  }, 1000)

}))

mock.onGet('/api/sets/getById').reply((request) => new Promise(((resolve) => {
  const {setId} = request?.params
  let dataItem = {}

  if(!setId){
    setTimeout(() => {
      resolve([200, null])
    }, 1000)
    return
  }

  const set = SurgicalTypesMock.find((item) => item.id === setId)
  if(set){
    dataItem = {...set}
    const surgicalTypeMedicalTools = {...SurgicalTypeMedicalToolsMock}
    const equipments = {...MedicalToolsMock}

    if(!dataItem.equipments || !dataItem.equipments.length){
      setTimeout(() => {
        resolve([200], { ...dataItem, equipments: { byId: {}, allIds: [], totalEquipments: 0, totalItems: 0 }})
      }, 1000)
      return
    }
    
    let byId = {}, allIds = []
    let totalItems = dataItem.equipments.map(equipmentId => {
      const amount = surgicalTypeMedicalTools[setId][equipmentId]//[equipmentId]
      byId[equipmentId] = { ...equipments[equipmentId], amount: amount }
      allIds.push(equipmentId)
      return amount
    }).reduce((acc, cur) => acc + cur, 0)

    dataItem["equipments"] = {
      byId: byId,
      allIds: allIds,
      totalEquipments: allIds.length,
      totalItems: totalItems
    }
    
    setTimeout(() => {
      resolve([200, {
        ...dataItem
      }])
    }, 1000)
  }
  else{
    setTimeout(() => {
      resolve([200, null])
    }, 1000)
  }
})))

mock.onPost('/api/sets/new').reply((request) => new Promise((resolve) => {
  const { name, active, description } = JSON.parse(request.data)
  const newId = uuidv4()

  const set = {
    id: newId,
    name,
    type: '',
    hospital: '',
    active,
    description,
    equipments: []
  }

  _.assign(SurgicalTypesMock, [...SurgicalTypesMock, set])

  const dateTimestamp = dayjs(new Date()).format()
  setTimeout(() => {
    resolve([200, {
      resultFlag: true,
      value: { 
        ...set, 
        lastModifiedTimestamp: dateTimestamp, 
        createdTimestamp: dateTimestamp 
      },
      errorCode: null,
      errorMessage: null,
      activityId: uuidv4()
    }])
  }, 1000)
}))

mock.onPost('/api/sets/update').reply((request) => new Promise((resolve) => {
  const { id, name, active, description } = JSON.parse(request.data)
  const set = SurgicalTypesMock.find((item) => item.id === id)

  _.assign(set, {
    name, 
    active, 
    description
  })

  setTimeout(() => {
    resolve([200, {
      resultFlag: true,
      value: { lastModifiedTimestamp: dayjs(new Date()).format() },
      errorCode: null,
      errorMessage: null,
      activityId: uuidv4()
    }])
  }, 1000)
}))

mock.onPost('/api/sets/update-equipments').reply((request) => new Promise((resolve) => {
  
  const { id, equipments } = JSON.parse(request.data)
  const set = SurgicalTypesMock.find((item) => item.id === id)
  
  let equipmentIds = [], resource = {}

  equipments.allIds.forEach(id => {
    const {amount} = equipments.byId[id]
    equipmentIds.push(id)
    resource[id] = amount
  })

  _.assign(set, {
    equipments: equipmentIds
  })

  SurgicalTypeMedicalToolsMock[id] = resource

  setTimeout(() => {
    resolve([200, {
      resultFlag: true,
      value: { lastModifiedTimestamp: dayjs(new Date()).format() },
      errorCode: null,
      errorMessage: null,
      activityId: uuidv4()  
    }])
  }, 1000)
}))

mock.onPost('/api/sets/remove').reply((request) => new Promise((resolve) => {
  const { id } = JSON.parse(request.data)

  _.assign(SurgicalTypesMock, _.reject(SurgicalTypesMock, { id: id }))
  _.assign(SurgicalTypeMedicalToolsMock, _.omit(SurgicalTypeMedicalToolsMock, id))

  setTimeout(() => {
    resolve([200, {
      resultFlag: true,
      value: { lastModifiedTimestamp: dayjs(new Date()).format() },
      errorCode: null,
      errorMessage: null,
      activityId: uuidv4()  
    }])
  }, 1000)
}))