import { EQUIPMENT_CREATE } from "src/actions/equipmentActions";

export const initialState = {
  isFetching: false,
  error: null,
  formStepData: {
    name: null,
    description: null,
    rfidCode: null,
    imageLists: {},
    imageMain: null,
  },
};

export default (state = initialState, { type, payload, error }) => {
  switch (type) {
    case EQUIPMENT_CREATE.PENDING:
      return {
        ...state,
        isFetching: true,
      };
    case EQUIPMENT_CREATE.SUCCESS:
      return {
        ...state,
        isFetching: false,
        formStepData: payload,
        error: null,
      };
    case EQUIPMENT_CREATE.FAILED:
      return {
        ...state,
        isFetching: false,
        error,
        formStepData: {},
      };
    default:
      return state;
  }
};
