/* eslint-disable react/no-array-index-key */
import React, {
  lazy,
  Suspense,
  Fragment
} from 'react';
import {
  Switch,
  Redirect,
  Route
} from 'react-router-dom';
import MainLayout from 'src/layouts/MainLayout';
import EditLayout from 'src/layouts/EditLayout';
import LoadingScreen from 'src/components/LoadingScreen';
import AuthGuard from 'src/components/AuthGuard';
import GuestGuard from 'src/components/GuestGuard';

const routesConfig = [
  {
    exact: true,
    path: '/',
    component: () => <Redirect to="/home" />
  },
  {
    exact: true,
    guard: AuthGuard,
    layout: MainLayout,
    path: '/home',
    component: lazy(() => import('src/views/pages/HomeView'))
  },
  {
    exact: true,
    guard: AuthGuard,
    layout: MainLayout,
    path: '/account',
    component: lazy(() => import('src/views/pages/AccountView'))
  },
  {
    exact: true,
    path: '/404',
    component: lazy(() => import('src/views/pages/Error404View'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/login',
    component: lazy(() => import('src/views/auth/LoginView'))
  },
  {
    exact: true,
    path: '/login-unprotected',
    component: lazy(() => import('src/views/auth/LoginView'))
  },
  {
    exact: true,
    guard: GuestGuard,
    path: '/register',
    component: lazy(() => import('src/views/auth/RegisterView'))
  },
  {
    exact: true,
    path: '/register-unprotected',
    component: lazy(() => import('src/views/auth/RegisterView'))
  },
  {
    exact: true,
    path: '/forgot-password',
    component: lazy(() => import('src/views/auth/ForgotPasswordView'))
  },
  {
    exact: true,
    path: '/reset-password',
    component: lazy(() => import('src/views/auth/ResetPasswordView'))
  },
  {
    path: '/sets',
    guard: AuthGuard,
    routes: [
      {
        exact: true,
        layout: MainLayout,
        path: '/sets',
        component: lazy(() => import('src/views/sets/ListView'))
      },
      {
        exact: true,
        layout: EditLayout,
        path: '/sets/create',
        component: lazy(()=> import('src/views/sets/CreateView'))
      },
      {
        exact: true,
        layout: EditLayout,
        path: '/sets/:id',
        component: lazy(()=> import('src/views/sets/CreateView'))
      },
      {
        exact: true,
        layout: EditLayout,
        path: '/sets/details/:id',
        component: lazy(()=> import('src/views/sets/DetailsView'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  },
  {
    path: '/equipments',
    guard: AuthGuard,
    layout: MainLayout,
    routes: [
      {
        exact: true,
        path: '/equipments',
        component: lazy(() => import('src/views/equipments/ListView'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  },
  {
    path: '/borrowing-history',
    guard: AuthGuard,
    routes: [
      {
        exact: true,
        layout: MainLayout,
        path: '/borrowing-history',
        component: lazy(() => import('src/views/borrowing-history/ListView'))
      },
      {
        exact: true,
        layout: EditLayout,
        path: '/borrowing-history/:id/detail',
        component: lazy(() => import('src/views/borrowing-history/DetailView'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  },
  {
    path: '/verify',
    guard: AuthGuard,
    layout: MainLayout,
    routes: [
      {
        exact: true,
        path: '/verify',
        component: lazy(() => import('src/views/verify/VerifyView'))
      },
      {
        component: () => <Redirect to="/404" />
      }
    ]
  },
  {
    path: '*',
    layout: MainLayout,
    routes: [      
      {
        component: () => <Redirect to="/404" />
      }
    ]
  }
];

const renderRoutes = (routes) => (routes ? (
  <Suspense fallback={<LoadingScreen />}>
    <Switch>
      {routes.map((route, i) => {
        const Guard = route.guard || Fragment;
        const Layout = route.layout || Fragment;
        const Component = route.component;

        return (
          <Route
            key={i}
            path={route.path}
            exact={route.exact}
            render={(props) => (
              <Guard>
                <Layout>
                  {route.routes
                    ? renderRoutes(route.routes)
                    : <Component {...props} />}
                </Layout>
              </Guard>
            )}
          />
        );
      })}
    </Switch>
  </Suspense>
) : null);

function Routes() {
  return renderRoutes(routesConfig);
}

export default Routes;