import mock from 'src/utils/mock'
import pagingService from 'src/services/pagingService'
import { BorrowingHistoryMock, EquipmentsMock } from './mock-data/borrowingHistory'

mock.onGet('/api/borrowing-history/fetch').reply((request) => new Promise((resolve) => {
  const { sort, page, limit } = request?.params
  const query = ""
  let dataItems = [...BorrowingHistoryMock]
  const filteredItems = pagingService.applyFilters(dataItems, query, ['surgicalType']);
  const sortedItems = pagingService.applySort(filteredItems, sort);
  const paginatedItems = pagingService.applyPagination(sortedItems, page, limit);
  
  setTimeout(() => {
    resolve([200, {
      data: [...paginatedItems],
      total: filteredItems.length,
      errors: null
    }]);
  }, 1000)

}))

mock.onGet('/api/borrowing-history/getById').reply((request) => new Promise((resolve) => {
  const {id} = request?.params
  let dataItems = [...BorrowingHistoryMock]

  const dataItem = dataItems.find((item) => item.id === id)
  setTimeout(()=> {
    resolve([200, {
      data: {...dataItem}
    }])
  }, 1000)
}))

mock.onGet('/api/borrowing-history/getEquipments').reply((request) => new Promise((resolve) => {
  const id = request.params?.id
  let dataItems = [...EquipmentsMock]

  const dataItem = dataItems.find((item) => item.borrowingTimeId === id)
  setTimeout(()=> {
    resolve([200, {
      data: {...dataItem}
    }])
  }, 1000)
}))

