import axios from 'src/utils/axios';
import { createActionSet } from 'src/helpers';

export const HISTORY_FETCH = createActionSet('@history/fetch');

export const getAllListHistory = (didCancel) => async (dispatch) => {
  
  let url = process.env.REACT_APP_API_URL + "/history";

  dispatch({
    type: HISTORY_FETCH.PENDING,
  });

  try {
    const [result] = await Promise.all([axios.get(url)]);
    if (!didCancel) {
      dispatch({
        type: HISTORY_FETCH.SUCCESS,
        payload: result.data,
      });
    }
  } catch (error) {
    if (!didCancel) {
      dispatch({
        type: HISTORY_FETCH.FAILED,
        error,
      });
    }
  }
};
