mock.onGet('/api/equipments/fetch').reply(
  request =>
    new Promise((resolve, reject) => {
      const { query, sort, page, limit } = request?.params;

      let dataItems = [...equipmentType.equipments];

      const filteredItems = pagingService.applyFilters(dataItems, query, [
        'name'
      ]);
      const sortedItems = pagingService.applySort(filteredItems, sort);
      const paginatedItems = pagingService.applyPagination(
        sortedItems,
        page,
        limit
      );
      setTimeout(() => {
        resolve([
          200,
          {
            data: [...paginatedItems],
            total: filteredItems.length,
            errors: null
          }
        ]);
      }, 1000);
    })
);

